import React, { Component } from 'react';
import './editer.css';
import { connect } from 'react-redux';
import {makeid} from '../fs.js'
import { Editor } from '@tinymce/tinymce-react';
import { toast } from 'react-toastify';
import {Segment,Button,Grid, Form, TextArea } from 'semantic-ui-react';

const icon_media=<svg  fill={'#2185d0'} width={'25px'} height={'25px'} style={{marginRight:'75px'}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 512"><path d="M618.9 160.8l-208 69.33C404.4 232.3 400 238.5 400 245.3v172.4C394.9 416.7 389.6 416 384 416c-35.35 0-64 21.49-64 48s28.65 48 64 48c35.35 0 64-21.49 64-48V319l144-48v82.7C586.9 352.7 581.6 352 576 352c-35.35 0-64 21.49-64 48s28.65 48 64 48c35.35 0 64-21.49 64-48V176C640 165.1 629.3 157.4 618.9 160.8zM176 392c-8.836 0-16-7.164-16-16V96H40.2C17.1 96 0 113.1 0 136.2v271.7C0 430.1 17.93 448 40.13 448h249.8c5.904-25.11 25.54-45.43 52.31-56H176zM104 384c0 4.418-3.582 8-8 8H64c-4.418 0-8-3.582-8-8v-32c0-4.418 3.582-8 8-8h32c4.418 0 8 3.582 8 8V384zM104 288c0 4.418-3.582 8-8 8H64C59.58 296 56 292.4 56 288V256c0-4.418 3.582-8 8-8h32c4.418 0 8 3.582 8 8V288zM104 192c0 4.418-3.582 8-8 8H64C59.58 200 56 196.4 56 192V160c0-4.418 3.582-8 8-8h32c4.418 0 8 3.582 8 8V192zM235.6 320H368V255.1L301.3 256C296.3 256 291.7 253.2 289.4 248.7c-2.275-4.473-1.846-9.844 1.111-13.9l58.33-80c2.514-3.445 6.518-5.479 10.78-5.479s8.261 2.033 10.77 5.479l18.63 25.55l52.29-78.42C443.8 98.23 447.1 96 452.4 96c4.457 0 8.621 2.227 11.09 5.938l41.98 62.97L608 130.8V44.67C608 19.1 588.5 0 564.4 0H235.6C211.5 0 192 19.1 192 44.67v230.7C192 300 211.5 320 235.6 320zM288 64c17.68 0 32 14.33 32 32S305.7 128 288 128C270.3 128 256 113.7 256 96S270.3 64 288 64z"/></svg>
class Editer extends Component {
  constructor (props) {
    super(props)
    // this.myRef1 = React.createRef();
    this.state = {
        data:'',
        selected_img:{
            type:'',//img_editer
        },
        loading:true,
        step:0,
        is_show_GPT_by_btn:false,
        data_bot:{
            mt_sp:'',
            key_des:'',
            dt_kh:'',
            cot_truyen:''
        }
    }
  }
  componentDidMount(){
    // setTimeout(()=>{
    //     window.tinymce.execCommand('mceSetContent', false ,this.props.data);
    //     this.setState({loading:false})
    // },1000)
    this.setState({
        data:this.props.data
    })
  }
  componentWillReceiveProps(nextProps){
        if(nextProps.img_result.length>0 && this.state.selected_img.type==nextProps.keyLock){
            this.setState({
                selected_img:{
                    type:'',//img_editer
                },
            });
            if(nextProps.img_text_result!=""){
                window.tinymce.execCommand('mceInsertContent', false ,"<figure class='image'> <img class='lazyload'  alt='"+nextProps.img_text_result+"' title='"+nextProps.img_text_result+"' src='"+nextProps.img_result[0].url300+"' data-srcset='"+nextProps.img_result[0].url+"' ><figcaption>"+nextProps.img_text_result+"</figcaption> </figure><p></p>")
            }else{
                window.tinymce.execCommand('mceInsertContent', false ,"<img class='lazyload' src='"+nextProps.img_result[0].url300+"' data-srcset='"+nextProps.img_result[0].url+"'><p></p>") 
            }
            this.props.closeAction();
        }
  }
 
  render() {
    let {data,loading,step,is_show_GPT_by_btn,data_bot}=this.state;
    let is_show_GPT_prompt_by_dk=false;
    let data_prompt=this.props.data_prompt_GPT;
    // console.log(" ~ file: Editer.js:53 ~ Editer ~ render ~ data_prompt:", data_prompt)
    if(this.props.data_prompt_GPT!=undefined&&this.props.data_prompt_GPT!=null){
        is_show_GPT_prompt_by_dk=true;
    }
    let is_show_buoc_1=false;
    if(data_bot.mt_sp!=''&&data_bot.key_des!=''&&data_bot.dt_kh!=''){
        is_show_buoc_1=true;
    }
      return (
        <React.Fragment>
            <div>
                <div className='wrap-editer'  style={(step==2||step==3)?{top:"25vh"}:{}}>
                    {/* <h3>XXX:</h3> */}
                    <div className='eidex' style={{margin:"0px 200px"}}>
                            <Grid>
                                <Grid.Column width={is_show_GPT_by_btn?10:16}>
                                    <Segment loading={loading}>
                                        <div className='add-img'>
                                        <div className='zdas'>
                                            <button className='btn-addMe'
                                                onClick={()=>{
                                                    let keyLock=makeid(6);
                                                    this.setState({
                                                        selected_img:{
                                                            type:keyLock,
                                                        }
                                                    });
                                                    this.props.openAction({
                                                        type:"OPEN",
                                                        is_muti_selected:false,
                                                        keyLock:keyLock,
                                                        show_description_img:true
                                                    })
                                                }}
                                            >
                                            {icon_media}<span>Add media</span>
                                            </button>
                                        </div>
                                        </div>
                                        <Editor
                                        tinymceScriptSrc={process.env.PUBLIC_URL + '/tinymce/tinymce.min.js'}
                                        // onInit={(evt, editor) => this.myRef1.current = editor}
                                        onEditorChange={(evt) => this.setState({data:evt})}
                                        value={data}
                                        init={{
                                            height: 650,
                                            menubar: true,
                                            relative_urls: false,
                                            remove_script_host: false,
                                            plugins: [
                                            'advlist', 'autolink', 'lists', 'link', 'image', 'charmap',
                                            'anchor', 'searchreplace', 'visualblocks', 'code', 'fullscreen',
                                            'insertdatetime', 'media', 'table', 'preview', 'help', 'wordcount'
                                            ],
                                            toolbar: 
                                            'blockquote | blocks |'+'fontsize bold italic underline forecolor backcolor | alignleft aligncenter alignright alignjustify'+'| unlink | link |' +
                                            '| bullist numlist ' +
                                            'removeformat |'+' code | image| fontsizeselect | insertfile'+'| table |',
                                            toolbar_mode: 'wrap',
                                            content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }',
                                            entity_encoding : "raw",
                                            image_dimensions: false,

                                        }}
                                        onLoadContent={() =>{
                                            setTimeout(()=>{
                                                window.tinymce.execCommand('mceSetContent', false ,this.props.data);
                                                this.setState({loading:false})
                                            },300)
                                        }}
                                        />
                                    </Segment>    
                                </Grid.Column>
                                {is_show_GPT_by_btn&&<Grid.Column width={6}>
                                    <Segment>
                                    <div className='wra-gpt'>
                                        {step>5&&<div>
                                            <span>Bc 7:Kinh nghim khi mua "{data_prompt.key_word}":</span><br/>
                                            {data_bot.cot_truyen!=""&&<Button content='Get data' icon={step>6?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    let {data}=this.state;
                                                    if(step==6){
                                                        data+=`<h2>Kinh nghim khi mua ${data_prompt.key_word}</h2>`;
                                                        this.setState({step:7,data:data});
                                                    }
                                                    let data_rs_text=`OK, lm rt tt. Tip n, hy ng vai l mt trong cc i tng mua sn phm c nu  trn, k v cu chuyn ca chnh mnh, c b cc r rng, v c dn  nh sau: "${data_bot.cot_truyen}". Lu , cu chuyn ny tp trung v SEO "${data_prompt.key_word}", nn hy phn b thm cc t kha lin quan  phn tich  trn vo cu chuyn, v hy sng to thm nhng tnh hung kch tnh cho cu chuyn tr nn li cun. C gng s dng cc t vit tt, thnh ng, cm t chuyn tip, thn t, t b ngha v t thng tc, ng thi trnh cc cm t lp li v cu trc cu khng t nhin. kt qu tr v l cc th html tng ng (chun SEO), khng gm <html>, <head>, <body>, <div>. (ni dung ti thiu 400 t)`;
                                                    toast.success('Copy thnh cng! bn past vo chat GPT.', { theme: "colored" });
                                                    navigator.clipboard.writeText(data_rs_text);
                                                }}
                                            />}
                                            <Form>
                                                <TextArea placeholder='ngi ny tm mua ging ng, phi ng o la chn khng bit chn loi ging g hay ging st, thng qua mt ngi bn ch dn, anh ta  bit n ni tht An Bnh c bn ging st gi r, anh ta lin h v c h tr rt tn tnh, ch  dch v tt, v t  anh ta rt hi lng, v gii thiu thm bn b' 
                                                    value={data_bot.cot_truyen}
                                                    onChange={(e,{value})=>{
                                                        let {data_bot}=this.state;
                                                        data_bot.cot_truyen=value;
                                                        this.setState({data_bot:data_bot})
                                                    }}
                                                />
                                            </Form>
                                            
                                        </div>}
                                         {step>4&&<div>
                                            <span>Bc 6:Hnh nh thc t sn phm:</span><br/>
                                            <Button content='Get data' icon={step>5?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    let {data}=this.state;
                                                    if(step==5){
                                                        data+=`<h2>Hnh nh thc t sn phm</h2>`;
                                                        this.setState({step:6,data:data});
                                                    }
                                                }}
                                            />
                                        </div>}
                                         {step>3&&<div>
                                            <span>Bc 5:{data_prompt.key_word} thch hp cho i tng no:</span><br/>
                                            <Button content='Get data' icon={step>4?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    let {data}=this.state;
                                                    if(step==4){
                                                        data+=`<h2>${data_prompt.key_word} thch hp cho i tng no</h2>`;
                                                        this.setState({step:5,data:data});
                                                    }
                                                    let data_rs_text=`OK, lm tt lm. Tip theo, t d liu ca google v thng tin sn phm  trn, hy lit k 8 i tng nn s dng "${data_prompt.key_word}", v gii thch v sao, cho thm v d minh ha. C gn s dng cc t kha lin quan v t kha chnh. Bi vit ti thiu 300 t, C gng s dng cc t vit tt, thnh ng, cm t chuyn tip, thn t, t b ngha v t thng tc, ng thi trnh cc cm t lp li v cu trc cu khng t nhin. kt qu tr v l cc th html tng ng (chun SEO), khng gm <html>, <head>, <body>, <div>. (ni dung ti thiu 400 t)`;
                                                    toast.success('Copy thnh cng! bn past vo chat GPT.', { theme: "colored" });
                                                    navigator.clipboard.writeText(data_rs_text);
                                                }}
                                            />
                                        </div>}
                                         {step>2&&<div>
                                            <span>Bc 4:c im ca "{data_prompt.key_word}":</span><br/>
                                            <Button content='Get data' icon={step>3?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    let {data}=this.state;
                                                    if(step==3){
                                                        data+=`<h2>Thng tin v ${data_prompt.key_word}</h2>`;
                                                        this.setState({step:4,data:data});
                                                    }
                                                    let data_rs_text=`OK, rt tt. Tip theo, t nhng thng tin sn phm nu  trn, hy kt hp vi d liu t google vi cc ni dung lin quan, hy phn tch chi tit cc thng tin sn phm  a ra  trn, nhm mc ch cho ngi dng hiu r v sn phm hn. iu kin t ra l ni dung xoay quanh cc t kha lin quan. C gng s dng cc t vit tt, thnh ng, cm t chuyn tip, thn t, t b ngha v t thng tc, ng thi trnh cc cm t lp li v cu trc cu khng t nhin. kt qu tr v l cc th html tng ng (chun SEO), khng gm <html>, <head>, <body>, <div>. (ni dung ti thiu 400 t)`;
                                                    toast.success('Copy thnh cng! bn past vo chat GPT.', { theme: "colored" });
                                                    navigator.clipboard.writeText(data_rs_text);
                                                }}
                                            />
                                        </div>}
                                        {step>1&&<div>
                                            <span>Bc 3:To M t ngn cho sn phm ny:</span><br/>
                                            <Button content='Get data' icon={step>2?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    if(step==2){
                                                        this.setState({step:3});
                                                    }
                                                    let data_rs_text=`OK, rt tt, tip n hy vit cho ti 2 mu tm tt thng tin sn phm (ti a 200 t). iu kin l cha t kha chnh: "${data_prompt.key_word}", v cc t khoa lin quan, ngn ng Li cung, kch thch t m cho i tng  nu  trn.`;
                                                    toast.success('Copy thnh cng! bn past vo chat GPT.', { theme: "colored" });
                                                    navigator.clipboard.writeText(data_rs_text);
                                                }}
                                            />
                                        </div>}
                                        {step>0&&<div>
                                            <span>Bc 2:To tiu  cho sn phm ny:</span><br/>
                                            <Button content='Get data' icon={step>1?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    if(step==1){
                                                        this.setState({step:2});
                                                    }
                                                    let data_rs_text=`OK, rt tt, tip n hy vit cho ti 10 mu tiu  gii thiu sn phm. iu kin l  s k t ph hp  hin th ln google, Li cung, kch thch t m cho i tng  nu  trn, Lu  : tiu  phi cha t kha chnh: "${data_prompt.key_word}".`;
                                                    toast.success('Copy thnh cng! bn past vo chat GPT.', { theme: "colored" });
                                                    navigator.clipboard.writeText(data_rs_text);
                                                }}
                                            />
                                        </div>}
                                    {is_show_buoc_1&&<div>
                                            <span>Bc 1:Thc hin to data cho chat GPT:</span><br/>
                                            <Button content='Get data' icon={step>0?'check':'right arrow'} labelPosition='right' className='mgt-gpt' 
                                                onClick={()=>{
                                                    let prices=`- Sn phm c thuc tnh ${data_prompt.attribute_name} v gi tng ng nh sau:
`;
                                                    data_prompt.table_price.forEach(e => {
                                                        prices+=`+ ${data_prompt.attribute_name} ${e.name} c gi l ${(Number(e.price_v)+Number(e.price_profit)).toLocaleString('vi-VN', {style : 'currency', currency : 'VND'})},
`;
                                                    });
                                                    let infors=`- Cc thng tin lin quan ca sn phm nh sau:
`;
                                                    data_prompt.table_infor.forEach(e => {
                                                        infors+=`+ ${e.name} : ${e.value},
`;
});

                                                        let data_rs_text=`Ti c sn phm vi cc thng tin nh sau:
${prices} ${infors} - M t thm v sn phm: ${data_bot.mt_sp}.
+ Ni dung ca sn phm xoay quanh t kha "${data_prompt.key_word}", mc ch l  ti u ha SEO trn kt qu tm kim ca google. t kha "${data_prompt.key_word}" c ngha l : "${data_bot.key_des}".
+ i tng khch hng: ${data_bot.dt_kh}.
Bn hy tr li "YES" nu bn  hiu cc d liu m ti a ra  trn.
T nhng thng tin  trn v d liu tm kim t google, hy lit k 15 t kha lin quan n sn phm ny.`
                                                    if(step==0){
                                                        this.setState({step:1});
                                                    }
                                                    toast.success('Copy thnh cng! bn past vo chat GPT.', { theme: "colored" });
                                                    navigator.clipboard.writeText(data_rs_text);
                                                }}
                                            />
                                        </div>}
                                        <div>
                                            <span>M t thm v sn phm:</span><br/>
                                            <Form>
                                                <TextArea placeholder='M t hnh dng, c im ni bt, chc nng ni bt...' 
                                                    value={data_bot.mt_sp}
                                                    onChange={(e,{value})=>{
                                                        let {data_bot}=this.state;
                                                        data_bot.mt_sp=value;
                                                        this.setState({data_bot:data_bot})
                                                    }}
                                                />
                                            </Form>
                                        </div>
                                        <div>
                                            <span> ngha ca t kha chnh: "{data_prompt.key_word}" l:</span><br/>
                                            <Form>
                                                <TextArea placeholder='Ging st bn  Gia Lai...' 
                                                    value={data_bot.key_des}
                                                    onChange={(e,{value})=>{
                                                        let {data_bot}=this.state;
                                                        data_bot.key_des=value;
                                                        this.setState({data_bot:data_bot})
                                                    }}
                                                />
                                            </Form>
                                        </div>
                                        <div>
                                            <span>i tng khch hng: "{data_prompt.key_word}" l:</span><br/>
                                            <Form>
                                                <TextArea placeholder='Hc sinh, sinh vin, ngi c thu nhp thp...'
                                                    value={data_bot.dt_kh}
                                                    onChange={(e,{value})=>{
                                                        let {data_bot}=this.state;
                                                        data_bot.dt_kh=value;
                                                        this.setState({data_bot:data_bot})
                                                    }}
                                                />
                                            </Form>
                                        </div>




                                    </div>
                                    </Segment>  
                                </Grid.Column>}
                            </Grid>
                    </div>
                    <div className='buttonz-editer'>
                        <button className="ui positive button ABZ bobo"
                            onClick={()=>{
                                 let {data}=this.state;
                                data=data.replace(new RegExp('"', 'g'), "'")
                                 this.props.rs_data(data)
                            }}
                        >Lu</button>
                        <button className="ui negative button ABZ bobo"
                            onClick={()=>this.props.close()}
                        >Hy</button>
                    </div>
                </div>
            </div>
            {is_show_GPT_prompt_by_dk&&<div className='gpt-wrap'>
                <b>Prompt chat GPT</b>
                <div className='re'>
                    <Button content={is_show_GPT_by_btn?"n":"Hin"} icon={is_show_GPT_by_btn>0?'eye':'eye slash'} labelPosition='right' className='mgt-gpt' 
                        onClick={()=>this.setState({is_show_GPT_by_btn:!is_show_GPT_by_btn})}
                    />
                </div>
            </div>}
        </React.Fragment>
      );
  }
}

// 
const mapStateToProps = (state) => ({
    img_result: state.img_result,
    img_text_result: state.img_text_result,
    keyLock:state.keyLock
});

const mapDispatchToProps = (dispatch,props) => ({
    openAction: (data) => dispatch(data),
    closeAction: (type) => dispatch({type:'CLOSE'}),
});
  
  export default connect(mapStateToProps, mapDispatchToProps)(Editer);
  